* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

.wrapperFooter {
  background-color: black;
  display: flex;
  width: 100%;
  height: 209px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.wrapperFooter .logoFooter {
  margin: 20px 0 50px 0;
}

/*tablette*/
/*mobile*/
@media (max-width: 768px) {
  .pFooter {
    width: 150px;
    text-align: center;
  }
  .wrapperFooter .logoFooter {
    margin-bottom: 10px;
  }
}
