* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');
  font-family: 'Montserrat', sans-serif;
}

.wrapperProductPage {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: auto;
}
.wrapperProductPage .host {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.wrapperProductPage .host .titleProduct {
  color: #ff6060;
  font-size: 36px;
  font-style: normal;
  margin-bottom: 5px;
}
.wrapperProductPage .host .hostProfil {
  display: flex;
  align-items: center;
}
.wrapperProductPage .host .hostProfil .hostName {
  width: 93px;
  height: 52px;
  text-align: right;
  color: #ff6060;
  text-align: right;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 142%;
}
.wrapperProductPage .host .hostProfil .hostPicture {
  width: 64px;
  height: 64px;
  margin-left: 10px;
  border-radius: 50%;
}
.wrapperProductPage .tagsStarContainer {
  display: flex;
  justify-content: space-between;
}
.wrapperProductPage .tagsStarContainer .tagsContainer {
  display: flex;
  align-items: center;
  gap: 10px;
}
.wrapperProductPage .tagsStarContainer .tagsContainer .tagsProduct {
  border-radius: 10px;
  background-color: #ff6060;
  color: #fff;
  padding: 3px 30px;
}
.wrapperProductPage .tagsStarContainer .starContainer {
  display: flex;
  justify-content: flex-end;
}
.wrapperProductPage .tagsStarContainer .star {
  width: 25px;
  margin-left: 10px;
  color: #ff6060;
}
.wrapperProductPage .tagsStarContainer .starGray {
  width: 25px;
  margin-left: 10px;
  color: #ccc;
}
.wrapperProductPage .hostProfilMobile {
  display: none;
}
.wrapperProductPage .descripEquipeContainer {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}
.wrapperProductPage .descripEquipeContainer .description,
.wrapperProductPage .descripEquipeContainer .equipement {
  text-align: left;
  width: 45%;
}

/*mobile*/
@media (max-width: 768px) {
  .wrapperProductPage {
    display: flex;
    flex-wrap: wrap;
    width: 90%;
  }
  .wrapperProductPage .host .titleProduct {
    width: 100%;
  }
  .wrapperProductPage .host .locationProduct {
    font-size: 26px;
  }
  .wrapperProductPage .host .hostProfil {
    display: none;
  }
  .wrapperProductPage .tagsStarContainer {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10px;
  }
  .wrapperProductPage .tagsStarContainer .tagsContainer {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    padding-bottom: 10px;
  }
  .wrapperProductPage .tagsStarContainer .tagsContainer .tagsProduct {
    padding: 3px 10px;
  }
  .wrapperProductPage .tagsStarContainer .contentVersionMobile {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
  .wrapperProductPage
    .tagsStarContainer
    .contentVersionMobile
    .starContainer
    .star,
  .wrapperProductPage
    .tagsStarContainer
    .contentVersionMobile
    .starContainer
    .starGray {
    width: 10px;
  }
  .wrapperProductPage .tagsStarContainer .hostProfilMobile {
    visibility: visible;
    display: flex;
    align-items: center;
  }
  .wrapperProductPage .tagsStarContainer .hostProfilMobile .hostNameMobile {
    width: 93px;
    height: 33px;
    text-align: right;
    color: #ff6060;
    text-align: right;
    font-size: 18px;
    font-style: normal;
  }
  .wrapperProductPage .tagsStarContainer .hostProfilMobile .hostPictureMobile {
    width: 50px;
    height: 50px;
    margin-left: 10px;
    border-radius: 50%;
  }
  .wrapperProductPage .descripEquipeContainer {
    margin-top: 2px;
    display: flex;
    flex-direction: column;
    margin-bottom: 27px;
  }
  .wrapperProductPage .descripEquipeContainer .description,
  .wrapperProductPage .descripEquipeContainer .equipement {
    width: 100%;
  }
}
